<template>
  <div id="container" class="container">
      <h4 style="text-align:center;margin-top:0.8rem;">欢迎注册</h4>
      <van-steps :active="1"
	   active-icon="success">
		  <van-step>选择范围</van-step>
		  <van-step>基本信息</van-step>
		  <van-step>实人认证</van-step>
		  <van-step>合同完成</van-step>
	  </van-steps>
	  
	  <van-panel title="请选择代理范围" v-if="curStep==0">
		  <van-picker class="proxyWaies"
		  :columns="proxyWaies"
		  @change="onProxyWayChange"
		  :default-index="proxyIdx"
		  />
	  </van-panel>
	  
	   <van-panel title="基本信息" v-if="curStep==1">
		 
		  <van-field
			v-model="realName"
			required
			clearable
			label="用户名"
			right-icon="question-o"
			placeholder="姓名"
			@click-right-icon="$toast('必填实人认证')"
		  />
		  
		   <van-field
			v-model="idNo"
			required
			clearable
			label="身份证"
			right-icon="question-o"
			placeholder="身份证编号"
			@click-right-icon="$toast('必填实人认证')"
		  />
		  
		    <van-field
			v-model="phoneNo"
			required
			clearable
			label="手机号"
			placeholder="手机号码"
		  />
		  
		   <van-field
			v-model="sms"
			required
			center
			clearable
			label="短信验证码"
			placeholder="请输入短信验证码"
		  >
			<van-button slot="button" size="small" type="primary">发送验证码</van-button>
		  </van-field>
		    
		  
		   <van-field
			v-model="email"
			clearable
			label="电子邮箱"
			placeholder="电子邮箱"
		  />
		
		  
		  <van-field
			v-model="proxyAreaName"
			required
			readonly
			autosize
			label="代理地址"
			placeholder="代理地址"
			@click="showAreaList"
		  />
		  
		  <van-field v-if="proxyIdx==1"
			v-model="proxyStreet"
			required
			autosize
			type="textarea"
			rows="3"
			label="代理街道"
			placeholder="街道名称"
		  />
		  
		  <van-field v-if="proxyIdx==2"
			v-model="proxyCommunity"
			required
			autosize
			type="textarea"
			rows="3"
			label="代理社区"
			placeholder="代理社区"
		  />
		  
		  <van-field
			v-model="userAddr"
			required
			autosize
			type="textarea"
			rows="3"
			label="申请人住址"
			placeholder="街道门牌、楼层房间号等信息"
		  />
		 
	   </van-panel>
     
	   <van-panel title="上传身份信息" v-if="curStep==2">
       	   
	   <div v-for="(cerImg,index) in cerImgsUploads" class="upload-img" :key="index">
			 <van-row>
			   <van-col>
					<van-uploader :name="index" :before-read="cerBeforeRead" :after-read="cerAfterRead">
					   <van-button icon="photo" type="primary">{{cerImg.name}}</van-button>
					</van-uploader>
				</van-col>
			 </van-row>
			 <van-row>
			   <van-col  class="upload-img">
				<van-image 
				  ref="imgRef"
				  width="75%"
				  height="75%"
				  :src="imageList[index]">
				   <template v-slot:loading>
					<van-loading type="spinner" size="20" />
				   </template>
				</van-image>
			   </van-col>
			 </van-row>
		</div>
		
	   </van-panel>
       <van-panel title="实人认证" v-if="curStep==3">
	      <h3>实人认证</h3>
		  <h2>姓名：{{realName}}</h2>
		  <h2>身份证编号{{idNo}}</h2>
		  <van-button @click="gotoVer">去认证</van-button>
       </van-panel>
	   <van-panel title="认证结果" v-if="curStep==4">
	      
	   </van-panel>
	 <van-row type="flex" v-show="curStep!=3" class="btns" justify="space-around">
	    <van-col span="6">
		   <van-button ref="btnPre" round type="primary" hairline  @click="preStep">上一步</van-button>
		</van-col>
        <van-col span="6">
		   <van-button ref="btnNxt" round type="primary" hairline  @click="nextStep">下一步</van-button>
		</van-col>
	  </van-row>
	  <van-popup v-model="showAreaPop" position="bottom">
	       <van-area
		   :area-list="areaList"
		   :item-height=25
		   @change="onAreaListChange"
		   @confirm="onAddrConfirm"
		   @cancel="closeAreaList"
		   :value="proxyAreaCode"/>
	  </van-popup>
	  
	 <iframe id="mainIframe" v-model="showVerPop"
                style="z-index:3; width:100%; height:100%; position:absolute; top: 0; left:0;"
                :src="faceUrl" frameborder="0">
	 </iframe>
	
  </div>
 
</template>

<script>
import {
  Tag,
  Col,
  Row,
  Icon,
  Cell,
  CellGroup,
  Swipe,
  Toast,
  SwipeItem,
  GoodsAction,
  GoodsActionIcon,
  GoodsActionButton,
  
  NavBar,
  Field,
  Picker,
  Button,
  Step,
  Steps,
  Panel,
  Popup,
  Area,
  Uploader,
  Image,
  Loading
} from 'vant';

import areaList from '../../assets/js/area';
export default {
  components: {
     
    [Tag.name]: Tag,
    [Col.name]: Col,
	[Row.name]: Row,
    [Icon.name]: Icon,
    [Cell.name]: Cell,
    [CellGroup.name]: CellGroup,
    [Swipe.name]: Swipe,
    [SwipeItem.name]: SwipeItem,
    [GoodsAction.name]: GoodsAction,
    [GoodsActionIcon.name]: GoodsActionIcon,
    [GoodsActionButton.name]: GoodsActionButton,
	
	[NavBar.name]:NavBar,
	[Field.name]:Field,
	[Button.name]:Button,
	[Picker.name]:Picker,
	[Step.name]:Step,
	[Steps.name]:Steps,
	[Panel.name]:Panel,
	[Toast.name]:Toast,
	[Popup.name]:Popup,
	[Area.name]:Area,
	[Uploader.name]:Uploader,
	[Image.name]:Image,
	[Loading.name]:Loading
  },

  data() {
    return {
       proxyIdx:0
	  ,curStep:0
	  ,proxyName:''
	  ,stepActive:1
	  ,proxyWaies:['区级代理(费用：48000元)', '街道代理(费用：38000元)', '社区代理(费用：28000元)']
	  ,realName:''
	  ,idNo:''
	  ,phoneNo:''
	  ,sms:''
	  ,email:''
	  ,province:'北京市'
	  ,city:'北京市'
	  ,district:'东城区'
	  ,proxyAreaCode:'110101'
	  ,proxyAreaName:'北京市-北京市-东城区'
	  ,proxyStreet:''
	  ,proxyCommunity:''
	  ,userAddr:''
	  ,faceUrl:'https://ida.webank.com/api/web/login?webankAppId=IDA5oD0T&version=1.0.0&nonce=65eeedaa8a94405e99343840379869be&orderNo=11569739877239&h5faceId=9a8815488569a862e6ac43c74c9d9e97&url=http%3A%2F%2Fdy.51redwing.net%2Fentre%2FverCallback&resultType=2&userId=1&sign=e446b34a2152fae28dbd11cc166c35eb57361422&from=browser&redirectType=1'
	  ,setps:[]
	  ,areaList: null
	  ,showAreaPop:false
	  ,showVerPop:false
	  ,imageList: [
        require('../../assets/images/f.jpg'),//0
		require('../../assets/images/b.jpg'),//1
		require('../../assets/images/z.jpg'),//2
      ]
	  ,fileTmp:null
	  ,cerImgsUploads:[{name:'身份证正面',imgSrc:'',ref:'imgRef',idx:0},{name:'身份证背面',imgSrc:'',ref:'imgRef',idx:0},{name:'芝麻信用图',imgSrc:'',ref:'imgRef',idx:0}]
    };
  },

  methods: {
    intUI(){ 
      document.getElementById("container").style.width = document.documentElement.clientWidth + 'px';
	  document.getElementById("container").style.height = document.documentElement.clientHeight + 'px';
	  this.areaList = areaList;
	   for (var i = 0; i < this.cerImgsUploads.length; i++) {  
         this.cerImgsUploads[i].imgSrc=this.imageList[i];
      }
	  
	}
	,nextStep(){
	   let t=this;
	   t.curStep++;
	}
	,preStep(){
	   let t=this;
	   t.curStep--;
	}
	,onProxyWayChange(picker, value, index) {
	  let t=this;
	  t.proxyIdx=index;
    }
	,showAreaList(){
	  let t=this;
	  t.showAreaPop=true;
	}
	,onAreaListChange(picker){
	   let val=picker.getValues();
	}
	,onAddrConfirm(e){
	   this.province = e[0].name
       this.city = e[1].name
       this.district = e[2].name
	   this.proxyAreaName=this.province+"-"+this.city+"-"+this.district
	   this.proxyAreaCode=e[2].code;
       this.showAreaPop = false
	}
	,closeAreaList(){
	   this.showAreaPop = false;
	},
    cerBeforeRead(file) {
      return true;
    },
	cerAfterRead(file,detail){
	    var idx=detail.name;
        this.$set(this.imageList, idx, file.content);
	}
	,gotoVer(){
	   console.log("Welcome to vue");
	   this.showVerPop=true;
	}
	,verCallBack(flag){
	   console.log('verCallBack'+flag);
	}
  }
  ,watch:{
      curStep(val) {
        if(val==0){
		  this.$refs.btnPre.disabled=true;
		}else{
		  this.$refs.btnPre.disabled=false;
		}
    }
  }
  ,computed:{
     computedArea(){
	   
	 }
	 
  }
  ,mounted(){
	  this.intUI();
	  this.$refs.btnPre.disabled=true;
	  
	  window["verCallBack"] = (flag) => {
		  this.verCallBack(flag);
	  };
	  
	  this.curStep=3;
   }
};
</script>

<style lang="less" scoped>
.container {
    min-height:100vh;
	//background:url('http://et.51redwing.net/gil/images/share-bg.jpg') no-repeat;
    background-size:cover;
	background-size: 100% 100%;
	background-position:center;
    height:100%;
    width:100%;
	
	
	//text-align:center;
	.btns{
	  margin-top:0.1rem;
	  .van-button{
	   width:3rem;
	  }
	}
	.van-row{
	  margin-bottom:0.1rem;
	}
	.upload-img{
	  text-align:center;
	  margin-left:0.1rem;
	}
}
</style>
